.form-container{
    width: 400px;
    padding: 1rem 2rem;
    text-align: center;
}

.form{
    background-color: white;
    box-shadow: 2px 2px #D3D3D3;
    display: flex;
    justify-content: left;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 5rem;
    border-radius: 5px;
}

.only-span{
    color: red;
}